<template>
  <div class="swiper_vessel">
    <swiper class="swiper" :options="swiperOption" ref="mySwiper">
      <swiper-slide class="swiper_item">
        <img src="../assets/images/swiper/01.webp" alt="">
      </swiper-slide>
      <swiper-slide class="swiper_item">
        <img src="../assets/images/swiper/02.webp" alt="">
      </swiper-slide>
      <swiper-slide class="swiper_item">
        <img src="../assets/images/swiper/03.webp" alt="">
      </swiper-slide>
      <swiper-slide class="swiper_item">
        <img src="../assets/images/swiper/04.webp" alt="">
      </swiper-slide>
      <swiper-slide class="swiper_item">
        <img src="../assets/images/swiper/05.jpg" alt="">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
      <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
  </div>
</template>

<script>
// 引入插件
import { swiper, swiperSlide } from "vue-awesome-swiper";
import "swiper/dist/css/swiper.css";

export default {
  name:'Swiper',
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOption: {
        loop: true,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        // 显示分页
        pagination: {
          el: ".swiper-pagination",
          clickable: true //允许分页点击跳转
        },
        // 设置点击箭头
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
    console.log("this is current swiper instance object", this.swiper);
    // this.swiper.slideTo(3, 1000, false);
  }
};
</script>
<style lang="scss" scoped>
  .swiper_vessel {
    margin-top: 10px;
    .swiper {
      max-width: 1000px;
      height: 500px;
      overflow: hidden;
      border-radius: 12px;
      .swiper_item {
        overflow: hidden;
        border-radius: 12px;
        height: 100%;
        // 调整轮播图的展示方式
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          // height: 100%;
          border-radius: 12px;
          width: 1000px;
        }
      }
      .swiper-button-next {
        // background-color: #fff;
        color: #fff;
      }
    }
  }
</style>