// 引入vue-router
import VueRouter from 'vue-router'

// 引入组件
// 主页
import HomeMain from '../pages/HomeMain'
// 养老院
import EndowmentMain from '../pages/EndowmentMain'
import AskAnswerMain from '../pages/AskAnswerMain'
import AboutUsMain from '../pages/AboutUsMain'
import Login from '../pages/Login'
import Asks from '../pages/Asks'
import AsksCommit from '../pages/AsksCommit'
import LoginEndowment from '../pages/LoginEndowment'
import Mine from '../pages/Mine'



// 创建并暴露路由
export default new VueRouter({
  routes:[
    {
      // 主页
      path:'/home',
      name:'Home',
      component:HomeMain
    },
    {
      // 养老院模块
      path: '/endowment',
      name:'Endowment',
      component:EndowmentMain,
      children: [
        {
          path:'login-endowment',
          component:LoginEndowment
        }
      ]
    },
    {
      // 问答模块
      path: '/ask-answer',
      name:'AskAnswer',
      component:AskAnswerMain,
      children: [
        {
          path:'asks',
          component:Asks
        },
        {
          path:'asks-commit',
          component:AsksCommit
        }
      ]
    },
    {
      // 关于我们模块
      path: '/about-us',
      name:'AboutUs',
      component:AboutUsMain
    },
    {
      // 登录页面
      path: '/login',
      name: "Login",
      component: Login
    },
    {
      // 个人页面
      path:'/mine',
      name:'Mine',
      component:Mine
    }
  ]
})

// 解决ElementUI导航栏中的vue-router在3.0版本以上重复点菜单报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}