<template>
  <div class="login_endowment">
    <Title title="养老院入驻"></Title>
    <div class="main_info">

      <!-- 养老院 名称 -->
      <div class="endowment_name_vessel">
        <div class="name_title">养老院名称</div>
        <el-input v-model="endowmentName" placeholder="请输入养老院名称"></el-input>
      </div>

      <!-- 养老院地址 -->
      <div class="endowment_address_vessel">
        <div class="address_title">养老院地址</div>
        <el-input v-model="endowmentAddress" placeholder="请输入养老院地址"></el-input>
      </div>

      <!-- 养老院联系方式 -->
      <div class="endowment_phone_vessel">
        <div class="phone_title">联系方式</div>
        <el-input v-model="endowmentPhone" placeholder="请输入联系方式"></el-input>
      </div>

      <!-- 养老院介绍 -->
      <div class="endowment_introduce_vessel">
        <div class="introduce_title">养老院介绍</div>
        <el-input
          type="textarea"
          placeholder="请输入养老院介绍"
          v-model="endowmentIntroduce"
          :show-word-limit="true"
          :autosize="true"
        >
        </el-input>
      </div>

      <!-- 养老院图片 -->
      <div class="endowment_images_vessel">
        <div class="images_title">养老院图片</div>
        <el-upload action="#" list-type="picture-card" :auto-upload="false">
          <i slot="default" class="el-icon-plus"></i>
          <div slot="file" slot-scope="{ file }">
            <img
              class="el-upload-list__item-thumbnail"
              :src="file.url"
              alt=""
            />
            <span class="el-upload-list__item-actions">
              <span
                class="el-upload-list__item-preview"
                @click="handlePictureCardPreview(file)"
              >
                <i class="el-icon-zoom-in"></i>
              </span>
              <span
                v-if="!disabled"
                class="el-upload-list__item-delete"
                @click="handleDownload(file)"
              >
                <i class="el-icon-download"></i>
              </span>
              <span
                v-if="!disabled"
                class="el-upload-list__item-delete"
                @click="handleRemove(file)"
              >
                <i class="el-icon-delete"></i>
              </span>
            </span>
          </div>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="" />
        </el-dialog>
      </div>

      <el-button type="primary" plain>提交</el-button>
    </div>
  </div>
</template>

<script>
import Title from "@/components/Title";
export default {
  name: "LoginEndowment",
  components: { Title },
  data() {
    return {
      // 养老院名称
      endowmentName:'',
      // 养老院地址
      endowmentAddress:'',
      // 养老院电话
      endowmentPhone:'',
      // 养老院介绍
      endowmentIntroduce:'',

      // 关于表单的
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
    };
  },
  mounted() {
    if (window.history && window.history.pushState) {
      // 向历史记录中插入了当前页
      history.pushState(null, null, document.URL);
      window.addEventListener("popstate", this.goBack, false);
    }
  },
  destroyed() {
    console.log('销毁了')
    this.$store.dispatch("letEndowmentListShow");
    window.removeEventListener("popstate", this.goBack, false);
  },
  methods: {
    // 浏览器按下返回键的时候执行
    goBack() {
      this.$store.dispatch("letEndowmentListShow");
      console.log("点击了浏览器的返回按钮");
      sessionStorage.clear();
      window.history.back();
      history.pushState(null, null, document.URL);
    },

    // 表单处理项
    // 删除模块
    handleRemove(file) {
      console.log(file);
    },
    // 预览模块
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 下载模块
    handleDownload(file) {
      console.log(file);
    },
  },
};
</script>

<style lang="scss" scoped>
.login_endowment {
  margin: 0 auto;
  padding-top: 1px;
  max-width: 1000px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  // height: 500px;

  .main_info {
    padding: 20px;
    margin-top: 20px;
    // background-color: #bfa;
    display: flex;
    flex-direction: column;
    // align-items: center;

    // 养老院名称
    .endowment_name_vessel,
    .endowment_address_vessel,
    .endowment_phone_vessel,
    .endowment_introduce_vessel,
    .endowment_images_vessel {
      // width: 100%;
      margin: 10px;
      // padding: 10px;
      margin-bottom: 15px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
    }

    .endowment_name_vessel .name_title,
    .endowment_address_vessel .address_title,
    .endowment_phone_vessel .phone_title,
    .endowment_introduce_vessel .introduce_title,
    .endowment_images_vessel .images_title{
      // margin: 10px;
      padding-bottom: 10px;
      font-size: 18px;
    }

    .endowment_images_vessel {
      margin-bottom: 30px;
    }
  }
}
</style>