<template>
  <!-- 养老院筛选 -->
  <div class="endowment_filter">
    <!-- 左侧标签区域 -->
    <div class="filter_title_vessel">
      <div class="title_area">地区</div>
      <div class="title_price">价格</div>
      <div class="title_kind">宠物种类</div>
    </div>

    <!-- 右侧真实筛选区 -->
    <div class="filter_content_vessel">
      <!-- 地区筛选 -->
      <div class="area_filter">
        <!-- <div class="area_item_active">全部</div> -->
        <div :class="areaClass">全部</div>
        <div class="city_select">
          <City @getLawyerListInfo="getPosition"></City>
        </div>
      </div>

      <!-- 价格筛选 -->
      <div class="price_filter">
        <div :class="priceClass[0]" @click="selectPrice(0)">全部</div>
        <div :class="priceClass[1]" @click="selectPrice(1)">价格区间1</div>
        <div :class="priceClass[2]" @click="selectPrice(2)">价格区间2</div>
        <div :class="priceClass[3]" @click="selectPrice(3)">价格区间3</div>
        <div :class="priceClass[4]" @click="selectPrice(4)">价格区间4</div>
        <div :class="priceClass[5]" @click="selectPrice(5)">价格区间5</div>
      </div>

      <!-- 宠物种类筛选 -->
      <div class="kind_filter">
        <div :class="kindClass[0]" @click="selectKind(0)">全部</div>
        <div :class="kindClass[1]" @click="selectKind(1)">宠物种类1</div>
        <div :class="kindClass[2]" @click="selectKind(2)">宠物种类2</div>
        <div :class="kindClass[3]" @click="selectKind(3)">宠物种类3</div>
      </div>
    </div>
  </div>
</template>

<script>
    // 地区选择组件
  import City from '../components/City.vue'
  export default {
    name: "EndowmentFilter",
    components: {
      City
    },
    data() {
      return {
        // 价格样式
        priceClass:['price_item_active','price_item','price_item', 'price_item', 'price_item', 'price_item'],
        // 宠物种类样式
        kindClass:['kind_item_active','kind_item', 'kind_item', 'kind_item'],
        // 位置
        position:[]
      }
    },
    computed: {
      // 地区的样式
      areaClass() {
        if (this.position[0]) {
          return 'area_item'
        } else {
          return 'area_item_active'
        }
      }
    },
    methods: {
      // 选择价格
      selectPrice(index) {
        for (let i = 0; i < this.priceClass.length; i++) {
          this.priceClass.splice(i, 1, 'price_item')
        }
        this.priceClass.splice(index, 1, 'price_item_active')
      },
      // 选择宠物种类
      selectKind(index) {
        for (let i = 0; i < this.kindClass.length; i++) {
          this.kindClass.splice(i, 1, 'kind_item')
        }
        this.kindClass.splice(index, 1, 'kind_item_active')
      },
      // 处理位置信息
      getPosition(obj) {
        if (obj[0]) {
          this.position.push(obj)
        } else {
          this.position.pop()
        }
      }
    }
  };
</script>

<style lang="scss" scoped>
