  .filter_title_vessel {
    height: 100%;
    height: 160px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    // background-color: red;
    font-size: 18px;
    min-width: 4em;
    // 统一样式
    .title_area,
