    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    // background-color: blue;

    // 地区筛选
    .area_filter {
      width: 100%;
      display: flex;
      align-items: center;
      // justify-content: space-between;
      .area_item {
