        color: #fff;
        background-color: #ff9671;
      }

    }

    // 价格筛选
    .price_filter {
      margin-top: 20px;
      display: flex;
      width: 100%;
      // justify-content: space-between;
      // 每一个价格区间
      .price_item {
        margin-right: 10px;
        width: 150px;
