<template>
  <div class="nav_bar_vessel">
    <div class="nav_bar">
      <div class="logo" @click="toHome(0)">LOGO</div>
      <div :class="this.$store.state.navBarClass[0]" @click="toHome(0)">首页</div>
      <div :class="this.$store.state.navBarClass[1]" @click="toEndowment(1)">养老院</div>
      <div :class="this.$store.state.navBarClass[2]" @click="toAskAnswer(2)">问答模块</div>
      <div :class="this.$store.state.navBarClass[3]" @click="toAboutUs(3)">关于我们</div>
      <div class="input_search" @click="toEndowment(1)">
        <el-input 
        v-model="searchTitle" 
        placeholder="搜索养老院"
        size="mini"
        suffix-icon="el-icon-search"
        ></el-input>
      </div>
      <div class="user_info" @click="toMine">
        <div class="user_name">用户名</div>
        <div class="head_img">
          <img src="https://picsum.photos/44/44?random=1" alt="头像">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name:'NavBar',
    data() {
      return {
        searchTitle:''
      }
    },
    methods: {
      // 跳转到首页
      toHome(index) {
        this.$store.commit('CHANGE_NAV', index)
        this.$emit('toHome')
      },
      // 跳转到养老院
      toEndowment(index) {
        // console.log(e)
        this.$store.commit('CHANGE_NAV', index)
        this.$emit('toEndowment')
      },
      // 跳转到问答模块
      toAskAnswer(index) {
        this.$store.commit('CHANGE_NAV', index)
        this.$emit('toAskAnswer')
      },
      // 跳转到关于我们
      toAboutUs(index) {
        this.$store.commit('CHANGE_NAV', index)
        this.$emit('toAboutUs')
      },
      // 跳转到个人主页
      toMine() {
        // this.$store.commit
        this.$store.commit('CLEAR_CLASS')
        this.$emit('toMine')
      }
    }
  }
</script>

<style lang="scss" scoped>
  // 导航
  .nav_bar_vessel {
    box-sizing: border-box;
    background-color: #FF9671;
    height: 60px;
    .nav_bar {
      font-size: 14px;
      font-weight: bold;
      height: 100%;
      max-width: 1200px;
      // min-width: 980px;
      margin: 0 auto;
      color: #eee;
      display: flex;
      align-items: center;
      justify-content: space-between;
      div:hover {
        cursor: pointer;
      }
      .logo{
        font-size: 18px;
        color: #F9F871;
      }
      .nav_item_active::after {
        content: '';
        display: block;
        float: left;
        margin-top: 10px;
        width: 100%;
        height: 2px;
        background-color: #fff;
      }
      .user_info{
        height: 100%;
        display: flex;
        align-items: center;
        .user_name {
          font-size: 12px;
        }
        .head_img{
          display: flex;
          justify-content: center;
          align-items: center;
          width: 44px;
          height: 44px;
          border-radius: 50%;
          overflow: hidden;
          background-color: #fff;
          margin-left: 10px;
        }
      }
    }
  }
</style>