<template>
  <div class="head">
    <!-- 顶部登录条 -->
    <LoginBar 
      v-if="$store.state.isShowHead"
      @toLogin="handleToLogin"
    ></LoginBar> 

    <!-- 顶部导航条 -->
    <NavBar
      v-if="$store.state.isShowHead"
      @toHome="handleToHome"
      @toEndowment="handleToEndowment"
      @toAskAnswer="handleToAskAnswer"
      @toAboutUs="handleToAboutUs"
      @toMine="handleToMine"
    ></NavBar>

    <!-- 路由展示区域 -->
    <router-view class="main_content"></router-view>

    <!-- 底部footer区域 -->
    <Footer v-if="$store.state.isShowHead"></Footer>
  </div>
</template>

<script>
  // 登录条
  import LoginBar from '../components/LoginBar.vue'
  // 导航条
  import NavBar from '../components/NavBar.vue'
  import Footer from '../components/Footer.vue'
  export default {
    name:'Head',
    data() {
      return {
      }
    },
    components:{
      LoginBar,
      NavBar,
      Footer
    },
    mounted() {
      this.$store.dispatch('letHeadShow')
      this.handleToHome()
    },
    methods: {
      // 实现跳转到首页
      handleToHome() {
        this.$store.dispatch('letHeadShow')
        this.$router.push({
          name:'Home'
        })
      },
      // 实现跳转到养老院
      handleToEndowment() {
        // this.$store.dispatch('letHeadHidden')
        this.$store.dispatch('letHeadShow')
        this.$store.dispatch('letEndowmentListShow')
        this.$router.push({
          name:'Endowment'
        })
      },
      // 实现跳转到问答模块
      handleToAskAnswer() {
        this.$store.dispatch('letHeadShow')
        this.$router.push({
          name:'AskAnswer'
        })
      },
      // 实现跳转到关于我们
      handleToAboutUs() {
        this.$store.dispatch('letHeadShow')
        this.$router.push({
          name:'AboutUs'
        })
      },
      // 实现跳转到登录页面
      handleToLogin() {
        this.$store.dispatch('letHeadHidden')
        this.$router.push({
          name:'Login'
        })
      },
      // 实现跳转到个人页面
      handleToMine() {
        this.$store.dispatch('letHeadShow')
        this.$router.push({
          name:'Mine'
        })
      }
    },
  }
</script>

<style scoped lang="scss">
  .main_content {
    // 防止底部footer和nav区域接触
    min-height: 100vh;
  }
</style>