// 引入vue
import Vue from 'vue'
// 引入vuex
import Vuex from 'vuex'

// 应用vuex
Vue.use(Vuex)

const actions = {
  // 让头部展示
  letHeadShow(content) {
    if (content.state.isShowHead === false) {
      content.commit('CHANGE_ISSHOWHEAD')
    }
  },
  // 让头部隐藏
  letHeadHidden(content) {
    console.log(content)
    if (content.state.isShowHead === true) {
      content.commit('CHANGE_ISSHOWHEAD')
    }
  },

  // 让养老院列表显示
  letEndowmentListShow(content) {
    if (content.state.isShowEndowmentList === false) {
      content.commit('CHANGE_ISSHOWENDOWMENTLIST')
    }
  },
  // 让养老院列表隐藏
  letEndowmentListHidden(content) {
    if (content.state.isShowEndowmentList === true) {
      content.commit('CHANGE_ISSHOWENDOWMENTLIST')
    }
  }
}

const mutations = {
  // 改变isShowHead的值
  CHANGE_ISSHOWHEAD(state) {
    state.isShowHead = !state.isShowHead
  },
  // 改变所选nav的类名
  CHANGE_NAV(state, index) {
    for (let i = 0; i < state.navBarClass.length; i++) {
      state.navBarClass[i].pop()
      state.navBarClass[i].pop()
      state.navBarClass[i].push('nav_item')
    }
    state.navBarClass[index].push('nav_item_active')
  },

  // 改变isShowEndowmentList的值
  CHANGE_ISSHOWENDOWMENTLIST(state) {
    state.isShowEndowmentList = !state.isShowEndowmentList
  },

  // 点击个人页面，清除了所有样式的选中效果
  CLEAR_CLASS(state) {
    for (let i = 0; i < state.navBarClass.length; i++) {
      state.navBarClass[i].pop()
      state.navBarClass[i].pop()
      state.navBarClass[i].push('nav_item')
    }
  }
}

